.todolist {
    height: max-content;
    border: 2px solid hotpink;
    border-radius: 5px;
    padding: 20px;
}

.task {
    font-weight: bold;
}

.task-done {
    text-decoration: line-through;
    opacity: 0.5;
}

.active-filter-btn {
    background-color: hotpink;
    font-weight: bold;
    color: white;
    border: 2px solid hotpink;
    border-radius: 3px;
}

.filter-btn {
    background-color: cornflowerblue;
    font-weight: bold;
    color: white;
    border: 2px solid hotpink;
    border-radius: 3px;
}

.filter-btn-container {
    display: flex;
    justify-content: space-between;
}

.input-error {
    border: 2px solid hotpink;
    outline: none;
}

.add-form {
    display: flex;
    justify-content: space-between;
    align-items: start;
}